import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import {
  Container,
  Button,
  Typography,
  Box,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  IconButton,
  TableHead,
  TableRow,
  Chip,
  Alert
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import DeleteIcon from '@mui/icons-material/Delete'

const PlacementList = () => {
  const [placements, setPlacements] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    fetchPlacements();
  }, []);


  const fetchPlacements = async () => {
    try {
      setLoading(true);
      const response = await axios.get('/api/placements');
      setPlacements(response.data);
      setError('');
    } catch (error) {
      console.error('Error fetching placements:', error);
      setError('Failed to load placements. Please check if the backend is running.');
    } finally {
      setLoading(false);
    }
  };

  const calculateFeeAmount = (placement) => {
    return placement.starting_salary * placement.fee_percentage;
  };

  if (loading) {
    return (
      <Container>
        <Typography>Loading placements...</Typography>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg">
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4">Placements</Typography>
        <Button
          variant="contained"
          startIcon={<AddIcon />}
          component={Link}
          to="/placements/new"
        >
          Record New Placement
        </Button>
      </Box>

      {error && (
        <Alert severity="error" sx={{ mb: 3 }}>
          {error}
        </Alert>
      )}

      {placements.length === 0 ? (
        <Paper sx={{ p: 4, textAlign: 'center' }}>
          <Typography variant="h6" gutterBottom>No Placements Found</Typography>
          <Typography color="textSecondary" paragraph>
            Start by recording your first placement.
          </Typography>
          <Button
            variant="contained"
            startIcon={<AddIcon />}
            component={Link}
            to="/placements/new"
          >
            Record First Placement
          </Button>
        </Paper>
      ) : (
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Recruiter</TableCell>
                <TableCell>Bank</TableCell>
                <TableCell>Salary</TableCell>
                <TableCell>Fee</TableCell>
                <TableCell>Total</TableCell>
                <TableCell>Paid to Recruiter</TableCell>
                <TableCell>Candidate</TableCell>
                <TableCell>Date</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {placements.map((placement) => {
                const feeAmount = calculateFeeAmount(placement);
                const commission = placement.commission || 0;
                const commissionRate = placement.employee_commission_rate ? 
                  (placement.employee_commission_rate * 100).toFixed(1) + '%' : 'N/A';

                return (
                  <TableRow key={placement.id}>
                    <TableCell>
                      <Chip
                        label={placement.employee_name || 'Unknown'}
                        size="small"
                        color="primary"
                        variant="outlined"
                      />
                    </TableCell>                    

                    <TableCell>
                      <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                        
                        <Typography>{placement.bank_name}</Typography>
                      </Box>
                    </TableCell>
                    <TableCell>
                      <Typography>
                        ${placement.starting_salary.toLocaleString(undefined, { minimumFractionDigits: 0, maximumFractionDigits: 0 })}
                      </Typography>
                    </TableCell>
                    <TableCell>

                        <Typography>
                          {placement.fee_percentage * 100}%
                        </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography>
                      ${(placement.starting_salary * placement.fee_percentage).toLocaleString(undefined, { minimumFractionDigits: 0, maximumFractionDigits: 0 })}
                    </Typography>

                    </TableCell>
                    <TableCell>
                      <Box>
                        <Typography color={"primary"}>
                          ${(placement.commission_amount).toLocaleString()}
                        </Typography>
                      </Box>
                    </TableCell>
                    <TableCell>
                      <Chip
                        label={placement.candidate_name || 'Unknown'}
                        size="small"
                        color="secondary"
                        variant="outlined"
                      />
                    </TableCell> 
                    <TableCell>
                      {new Date(placement.placement_date).toLocaleDateString()}
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
      )}

      {placements.length > 0 && (
        <Box sx={{ mt: 3, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <Typography variant="body2" color="textSecondary">
            Total Placements: {placements.length}
          </Typography>
        </Box>
      )}
    </Container>
  );
};

export default PlacementList;