import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import {
  Container,
  TextField,
  Button,
  Typography,
  Paper,
  Grid,
  MenuItem,
  Alert,
  Card,
  CardContent,
  List,
  ListItem,
  ListItemText,
  Divider,
  Chip,
  LinearProgress
} from '@mui/material';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';

const PlacementForm = () => {
  const navigate = useNavigate();
  const [employees, setEmployees] = useState([]);
  const [selectedEmployee, setSelectedEmployee] = useState(null);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [commissionPreview, setCommissionPreview] = useState(null);
  const [isCalculating, setIsCalculating] = useState(false);
  
  const [placement, setPlacement] = useState({
    candidate_name: '',
    bank_name: '',
    starting_salary: '',
    fee_percentage: '',
    employee_id: '',
    placement_date: new Date()
  });

  useEffect(() => {
    fetchEmployees();
  }, []);

  const fetchEmployees = async () => {
    try {
      const response = await axios.get('/api/employees');
      setEmployees(response.data);
    } catch (error) {
      console.error('Error fetching employees:', error);
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    const updatedPlacement = {
      ...placement,
      [name]: value
    };
    
    setPlacement(updatedPlacement);
    
    if (name === 'employee_id') {
      const employee = employees.find(emp => emp.id === parseInt(value));
      setSelectedEmployee(employee);
      setCommissionPreview(null);
    }
    
    if (name === 'starting_salary' || name === 'fee_percentage' || name === 'employee_id') {
      if (updatedPlacement.starting_salary && 
          updatedPlacement.fee_percentage && 
          updatedPlacement.employee_id) {
        calculateCommissionPreview(updatedPlacement);
      } else {
        setCommissionPreview(null);
      }
    }
  };

  const handleDateChange = (date) => {
    setPlacement(prev => ({
      ...prev,
      placement_date: date
    }));
  };

  const calculateCommissionPreview = async (placementData) => {
    const salary = parseFloat(placementData.starting_salary);
    const feePercent = parseFloat(placementData.fee_percentage);
    const employeeId = parseInt(placementData.employee_id);
    
    if (!salary || !feePercent || !employeeId || isNaN(salary) || isNaN(feePercent)) {
      setCommissionPreview(null);
      return;
    }
    
    const feeAmount = salary * (feePercent / 100);
    
    setIsCalculating(true);
    try {
      const response = await axios.post(`/api/employees/${employeeId}/calculate-commission`, {
        fee_amount: feeAmount
      });
      setCommissionPreview(response.data);
    } catch (error) {
      console.error('Error calculating commission preview:', error);
      setCommissionPreview(null);
    } finally {
      setIsCalculating(false);
    }
  };

  const calculateFeeAmount = () => {
    const salary = parseFloat(placement.starting_salary) || 0;
    const feePercent = parseFloat(placement.fee_percentage) || 0;
    return salary * (feePercent / 100);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setSuccess('');
    
    if (!placement.employee_id) {
      setError('Please select an employee');
      return;
    }
    
    try {
      const placementData = {
        ...placement,
        starting_salary: parseFloat(placement.starting_salary),
        fee_percentage: parseFloat(placement.fee_percentage),
        employee_id: parseInt(placement.employee_id),
        placement_date: placement.placement_date.toISOString()
      };
      
      const response = await axios.post('/api/placements', placementData);
      
      const result = response.data;
      setSuccess(`
        Placement recorded successfully.
         Fee Amount: $${(placementData.starting_salary * (placementData.fee_percentage / 100)).toFixed(2)}
         Total Commission: $${result.commission_result.total_commission.toFixed(2)}
         Employee's new cumulative: $${result.employee.cumulative_commission_after.toFixed(2)}
      `);
      
      // Reset form
      setPlacement({
        candidate_name: '',
        bank_name: '',
        starting_salary: '',
        fee_percentage: '',
        employee_id: '',
        placement_date: new Date()
      });
      setSelectedEmployee(null);
      setCommissionPreview(null);
      
      setTimeout(() => navigate('/placements'), 3000);
    } catch (error) {
      console.error('Error recording placement:', error);
      setError(error.response?.data?.error || 'Error recording placement. Please try again.');
    }
  };

  const getRateColor = (rate) => {
    if (rate < 0.2) return 'success';
    if (rate < 0.3) return 'info';
    if (rate < 0.4) return 'warning';
    return 'error';
  };

  return (
    <LocalizationProvider dateAdapter={AdapterDateFns}>
      <Container maxWidth="lg">
        <Typography variant="h4" gutterBottom>Record New Placement</Typography>
        
        <Grid container spacing={3}>
          <Grid item xs={12} md={6}>
            <Paper elevation={3} sx={{ p: 3 }}>
              <Typography variant="h6" gutterBottom>Placement Details</Typography>
              
              {error && <Alert severity="error" sx={{ mb: 2 }}>{error}</Alert>}
              {success && <Alert severity="success" sx={{ mb: 2 }}>{success}</Alert>}
              
              <form onSubmit={handleSubmit}>
                <Grid container spacing={2}>
                  <Grid item xs={12} md={6}>
                    <TextField
                      fullWidth
                      label="Candidate (First and Last"
                      name="candidate_name"
                      value={placement.candidate_name}
                      onChange={handleInputChange}
                      required
                    />
                  </Grid>
                  
                  <Grid item xs={12} md={6}>
                    <TextField
                      fullWidth
                      label="Bank"
                      name="bank_name"
                      value={placement.bank_name}
                      onChange={handleInputChange}
                      required
                    />
                  </Grid>
                  
                  <Grid item xs={12} md={6}>
                    <TextField
                      fullWidth
                      label="Salary ($)"
                      name="starting_salary"
                      type="number"
                      value={placement.starting_salary}
                      onChange={handleInputChange}
                      required
                      InputProps={{
                        inputProps: { min: 0, step: 1000 }
                      }}
                    />
                  </Grid>
                  
                  <Grid item xs={12} md={6}>
                    <TextField
                      fullWidth
                      label="Fee (%)"
                      name="fee_percentage"
                      type="number"
                      value={placement.fee_percentage}
                      onChange={handleInputChange}
                      required
                      InputProps={{
                        inputProps: { min: 0, max: 100, step: 0.01 }
                      }}
                    />
                  </Grid>
                  
                  <Grid item xs={12} md={6}>
                    <TextField
                      fullWidth
                      select
                      label="Recruiter"
                      name="employee_id"
                      value={placement.employee_id}
                      onChange={handleInputChange}
                      required
                    >
                      <MenuItem value="">Select Recruiter</MenuItem>
                      {employees.map((employee) => (
                        <MenuItem key={employee.id} value={employee.id}>
                          {employee.name}
                        </MenuItem>
                      ))}
                    </TextField>
                  </Grid>
                  
                  <Grid item xs={12} md={6}>
                    <DatePicker
                      label="Placement Date"
                      value={placement.placement_date}
                      onChange={handleDateChange}
                      renderInput={(params) => <TextField {...params} fullWidth />}
                    />
                  </Grid>
                  
                  <Grid item xs={12}>
                    <Button
                      type="submit"
                      variant="contained"
                      color="primary"
                      fullWidth
                      size="large"
                      disabled={!placement.employee_id || !placement.starting_salary || !placement.fee_percentage}
                    >
                      Record Placement
                    </Button>
                  </Grid>
                </Grid>
              </form>
            </Paper>
          </Grid>
          
          <Grid item xs={12} md={6}>
            {isCalculating ? (
              <Paper elevation={3} sx={{ p: 3 }}>
                <Typography variant="h6" gutterBottom>Calculating Commission...</Typography>
                <LinearProgress />
              </Paper>
            ) : commissionPreview && commissionPreview.employee && commissionPreview.commission_result ? (
              <Paper elevation={3} sx={{ p: 3 }}>
                <Typography variant="h6" gutterBottom sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                   Commission Breakdown
                </Typography>
                
                <Card variant="outlined" sx={{ mb: 2 }}>
                  <CardContent>
                    <Grid container spacing={1}>
                      <Grid item xs={6}>
                        <Typography variant="subtitle2">Salary x fee:</Typography>
                      </Grid>
                      <Grid item xs={6}>
                        <Typography align="right">
                          ${calculateFeeAmount().toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}
                        </Typography>
                      </Grid>
                      
                    <Grid item xs={6}>
                    <Typography variant="subtitle2">Current revenue generated YTD:</Typography>
                    </Grid>
                    <Grid item xs={6}>
                    <Typography align="right">
                    ${commissionPreview.employee.cumulative_fees?.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}
                    </Typography>
                    </Grid>
                      
                      <Grid item xs={6}>
                        <Typography variant="subtitle2">Current Rate:</Typography>
                      </Grid>
                      <Grid item xs={6}>
                        <Typography align="right">
                          <Chip 
                            label={`${(commissionPreview.employee.current_rate || 0).toFixed(1)}%`}
                            size="small"
                            color={getRateColor((commissionPreview.employee.current_rate || 0) / 100)}
                          />
                        </Typography>
                      </Grid>
                      
                      <Divider sx={{ my: 1, width: '100%' }} />
                      
                      <Grid item xs={6}>
                        <Typography variant="subtitle2">Pay to recruiter:</Typography>
                      </Grid>
                      <Grid item xs={6}>
                        <Typography align="right" variant="h6" color="primary">
                          ${(commissionPreview.commission_result.total_commission || 0).toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}
                        </Typography>
                      </Grid>
                      

                      
                      <Grid item xs={6}>
                        <Typography variant="subtitle2">New Rate:</Typography>
                      </Grid>
                      <Grid item xs={6}>
                        <Typography align="right">
                          <Chip 
                            label={`${((commissionPreview.commission_result.new_rate || 0) * 100).toFixed(1)}%`}
                            size="small"
                            color={getRateColor(commissionPreview.commission_result.new_rate || 0)}
                            variant="outlined"
                          />
                        </Typography>
                      </Grid>
                    </Grid>
                  </CardContent>
                </Card>
                
                {commissionPreview.commission_result.breakdown && commissionPreview.commission_result.breakdown.length > 0 && (
                  <Card variant="outlined">
                    <CardContent>
                      <Typography variant="subtitle2" gutterBottom sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                         Tier Breakdown
                      </Typography>
                      <List dense>
                        {commissionPreview.commission_result.breakdown.map((segment, index) => (
                          <React.Fragment key={index}>
                            <ListItem alignItems="flex-start">
                              <ListItemText
                                primary={`Tier ${segment.segment || index + 1}`}
                                secondary={
                                  <>
                                    <Typography component="span" variant="body2" color="text.primary">
                                      {segment.description || `$${segment.fee_amount?.toFixed(2) || '0.00'} at ${(segment.rate || 0) * 100}%`}
                                    </Typography>
                                    <br />
                                    <Typography component="span" variant="caption" color="text.secondary">
                                      Commission: ${segment.commission?.toFixed(2) || '0.00'}
                                    </Typography>
                                    <br />
                                    <Typography component="span" variant="caption" color="text.secondary">
                                      Cumulative: ${segment.from_cumulative_fees?.toFixed(2) || '0.00'}  ${segment.to_cumulative_fees?.toFixed(2) || '0.00'}
                                    </Typography>
                                  </>
                                }
                              />
                            </ListItem>
                            {index < commissionPreview.commission_result.breakdown.length - 1 && <Divider />}
                          </React.Fragment>
                        ))}
                      </List>
                    </CardContent>
                  </Card>
                )}
                
              </Paper>
            ) : (
              <Paper elevation={3} sx={{ p: 3, textAlign: 'center' }}>
                <Typography color="textSecondary">
                  Enter placement details to see commission breakdown
                </Typography>
              </Paper>
            )}
          </Grid>
        </Grid>
      </Container>
    </LocalizationProvider>
  );
};

export default PlacementForm;