import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import axios from 'axios';
import {
  Container,
  TextField,
  Button,
  Typography,
  Box,
  Paper,
  Grid,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Alert,
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import DeleteIcon from '@mui/icons-material/Delete';

const EmployeeForm = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const isEdit = !!id;
  
  const [employee, setEmployee] = useState({
    name: '',
    email: '',
    phone: '',
    commission_structure: {
      base_rate: '',
      tiers: [],
      cap: ''
    }
  });
  
  const [newTier, setNewTier] = useState({ threshold: 0, rate: 0 });
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  useEffect(() => {
    if (isEdit) {
      fetchEmployee();
    }
  }, [id]);

  const fetchEmployee = async () => {
    try {
      const response = await axios.get(`/api/employees/${id}`);
      setEmployee(response.data);
    } catch (error) {
      console.error('Error fetching employee:', error);
      setError('Failed to load recruiter data');
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setEmployee(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleCommissionChange = (field, value) => {
    const decimalValue = parseFloat(value) / 100;


    setEmployee(prev => ({
      ...prev,
      commission_structure: {
        ...prev.commission_structure,
        [field]: isNaN(decimalValue) ? 0 : decimalValue
        
      }
    }));
  };

  const handleAddTier = () => {
    const threshold = parseFloat(newTier.threshold);
    const rate = parseFloat(newTier.rate);
    
    if (threshold <= 0 || rate <= 0 || isNaN(threshold) || isNaN(rate)) {
      setError('Threshold and rate must be valid positive numbers');
      return;
    }
    
    setEmployee(prev => ({
      ...prev,
      commission_structure: {
        ...prev.commission_structure,
        tiers: [
          ...prev.commission_structure.tiers,
          {
            threshold: threshold,
            rate: rate / 100
          }
        ].sort((a, b) => a.threshold - b.threshold)
      }
    }));
    
    setNewTier({ threshold: 0, rate: 0 });
    setError('');
  };

  const handleRemoveTier = (index) => {
    setEmployee(prev => ({
      ...prev,
      commission_structure: {
        ...prev.commission_structure,
        tiers: prev.commission_structure.tiers.filter((_, i) => i !== index)
      }
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setSuccess('');
    
    try {
      if (isEdit) {
        await axios.put(`/api/employees/${id}`, employee);
        setSuccess('Recruiter updated successfully.');
      } else {
        await axios.post('/api/employees', employee);
        setSuccess('Recruiter created successfully.');
      }
      
      setTimeout(() => {
        navigate('/employees');
      }, 1500);
    } catch (error) {
      console.error('Error saving recruiter:', error);
      setError(error.response?.data?.error || 'Error saving recruiter. Please try again.');
    }
  };

  return (
    <Container maxWidth="md">
      <Paper elevation={3} sx={{ p: 4, mt: 4 }}>
        <Typography variant="h5" gutterBottom>
          {isEdit ? 'Edit Recruiter' : 'Add New Recruiter'}
        </Typography>
        
        {error && <Alert severity="error" sx={{ mb: 2 }}>{error}</Alert>}
        {success && <Alert severity="success" sx={{ mb: 2 }}>{success}</Alert>}
        
        <form onSubmit={handleSubmit}>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <TextField
                fullWidth
                label="Recruiter Name"
                name="name"
                value={employee.name}
                onChange={handleInputChange}
                required
              />
            </Grid>
            
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label="Email"
                name="email"
                type="email"
                value={employee.email}
                onChange={handleInputChange}
                required
              />
            </Grid>
            
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label="Phone"
                name="phone"
                value={employee.phone}
                onChange={handleInputChange}
              />
            </Grid>
            
            <Grid item xs={12}>
              <Typography variant="h6" gutterBottom sx={{ mt: 2 }}>
                Commission Structure
              </Typography>
            </Grid>
            
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label="Base Rate (%)"
                type="number"
                value={employee.commission_structure.base_rate * 100 || ''}
                onChange={(e) => handleCommissionChange('base_rate', e.target.value)}
                required
                InputProps={{
                  inputProps: { min: 0, max: 100, step: 5 }
                }}
              />
            </Grid>
            
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label="Maximum Rate (%)"
                type="number"
                value={employee.commission_structure.cap * 100 || ''}
                onChange={(e) => handleCommissionChange('cap', e.target.value)}
                InputProps={{
                  inputProps: { min: 0, max: 100, step: 5 }
                }}
              />
            </Grid>
            
            <Grid item xs={12}>
              <Typography variant="subtitle1" gutterBottom sx={{ mt: 2 }}>
                Tiered Commission Structure
              </Typography>
              
              {employee.commission_structure.tiers.length > 0 ? (
                <TableContainer component={Paper} variant="outlined" sx={{ mb: 2 }}>
                  <Table size="small">
                    <TableHead>
                      <TableRow>
                        <TableCell><strong>Threshold Amount ($)</strong></TableCell>
                        <TableCell><strong>Commission Rate (%)</strong></TableCell>
                        <TableCell><strong>Actions</strong></TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {employee.commission_structure.tiers.map((tier, index) => (
                        <TableRow key={index}>
                          <TableCell>${tier.threshold.toLocaleString()}</TableCell>
                          <TableCell>{(tier.rate * 100).toFixed(2)}%</TableCell>
                          <TableCell>
                            <IconButton 
                              size="small" 
                              onClick={() => handleRemoveTier(index)}
                              color="error"
                            >
                              <DeleteIcon />
                            </IconButton>
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </TableContainer>
              ) : (
                <Typography variant="body2" color="textSecondary" sx={{ mb: 2 }}>
                  No tiers added yet. Recruiter will earn the base rate only.
                </Typography>
              )}
              
              <Grid container spacing={2} alignItems="center">
                <Grid item xs={12} md={5}>
                  <TextField
                    fullWidth
                    label="Threshold Amount ($)"
                    type="number"
                    value={newTier.threshold}
                    onChange={(e) => setNewTier(prev => ({ ...prev, threshold: e.target.value }))}
                    InputProps={{
                      inputProps: { min: 0, step: 1000 }
                    }}
                  />
                </Grid>
                <Grid item xs={12} md={5}>
                  <TextField
                    fullWidth
                    label="Commission Rate (%)"
                    type="number"
                    value={newTier.rate}
                    onChange={(e) => setNewTier(prev => ({ ...prev, rate: e.target.value }))}
                    InputProps={{
                      inputProps: { min: 0, max: 100, step: 0.01 }
                    }}
                  />
                </Grid>
                <Grid item xs={12} md={2}>
                  <Button
                    variant="outlined"
                    startIcon={<AddIcon />}
                    onClick={handleAddTier}
                    fullWidth
                  >
                    Add Tier
                  </Button>
                </Grid>
              </Grid>
              
              <Typography variant="caption" color="textSecondary" sx={{ mt: 1, display: 'block' }}>
                Note: Tiers will be applied when cumulative earnings reach the threshold amount.
              </Typography>
            </Grid>
            
            <Grid item xs={12} sx={{ mt: 2 }}>
              <Box sx={{ display: 'flex', gap: 2, justifyContent: 'flex-end' }}>
                <Button 
                  variant="outlined" 
                  onClick={() => navigate('/employees')}
                >
                  Cancel
                </Button>
                <Button 
                  type="submit" 
                  variant="contained" 
                  color="primary"
                >
                  {isEdit ? 'Update Recruiter' : 'Add Recruiter'}
                </Button>
              </Box>
            </Grid>
          </Grid>
        </form>
      </Paper>
    </Container>
  );
};

export default EmployeeForm;