import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import Dashboard from './components/Dashboard';
import EmployeeForm from './components/EmployeeForm';
import EmployeeList from './components/EmployeeList';
import PlacementForm from './components/PlacementForm';
import PlacementList from './components/PlacementList';
import { AppBar, Toolbar, Typography, Container, Box, Button, Avatar } from '@mui/material';

function App() {
  return (
    <Router>
      <Box sx={{ flexGrow: 1 }}>
        <AppBar position="static">
          <Toolbar>
                    <Box 
              component={Link} 
              to="/" 
              sx={{ 
                display: 'flex', 
                alignItems: 'center', 
                textDecoration: 'none', 
                color: 'inherit',
                mr: 3 
              }}
            >
              <img 
                src="/nowordlogo.png" 
                alt="Company Logo" 
                style={{ 
                  height: 60, 
                  marginRight: 10,
                  objectFit: 'contain'
                }}
              />
                <Typography variant="h5" component="div" sx={{ 
                  fontWeight: 'bold',
                    letterSpacing: '0.5px'
                }}>
                Commission Tracker
              </Typography>
              </Box>
            <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                
            </Typography>
            <Button color="inherit" component={Link} to="/">Dashboard</Button>
            <Button color="inherit" component={Link} to="/employees">Recruiters</Button>
            <Button color="inherit" component={Link} to="/placements">Placements</Button>
          </Toolbar>
        </AppBar>
        
        <Container maxWidth="xl" sx={{ mt: 4 }}>
          <Routes>
            <Route path="/" element={<Dashboard />} />
            <Route path="/employees" element={<EmployeeList />} />
            <Route path="/employees/new" element={<EmployeeForm />} />
            <Route path="/employees/edit/:id" element={<EmployeeForm />} />
            <Route path="/placements" element={<PlacementList />} />
            <Route path="/placements/new" element={<PlacementForm />} />
          </Routes>
        </Container>
      </Box>
    </Router>
  );
}

export default App;