import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import {
  Container,
  Button,
  Typography,
  Box,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Chip,
  Alert,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  DialogContentText,
  Tooltip
} from '@mui/material';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import PersonAddIcon from '@mui/icons-material/PersonAdd';
import InfoIcon from '@mui/icons-material/Info';

const EmployeeList = () => {
  const [employees, setEmployees] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [deleteDialog, setDeleteDialog] = useState({ open: false, employeeId: null, employeeName: '' });
  const navigate = useNavigate();

  useEffect(() => {
    fetchEmployees();
  }, []);

  const fetchEmployees = async () => {
    try {
      setLoading(true);
      const response = await axios.get('/api/employees');
      setEmployees(response.data);
      setError('');
    } catch (error) {
      console.error('Error fetching recruiters:', error);
      setError('Failed to load recruiters. Please check if the backend is running.');
    } finally {
      setLoading(false);
    }
  };

  const handleDeleteClick = (employeeId, employeeName) => {
    setDeleteDialog({
      open: true,
      employeeId,
      employeeName
    });
  };

  const handleDeleteConfirm = async () => {
    try {
      await axios.delete(`/api/employees/${deleteDialog.employeeId}`);
      fetchEmployees(); // Refresh the list
      setDeleteDialog({ open: false, employeeId: null, employeeName: '' });
    } catch (error) {
      console.error('Error deleting recruiter:', error);
      setError('Failed to delete recruiter.');
    }
  };

  const handleDeleteCancel = () => {
    setDeleteDialog({ open: false, employeeId: null, employeeName: '' });
  };

const formatCommissionStructure = (employee) => {
  const structure = employee.commission_structure;
  const baseRate = (structure.base_rate * 100).toFixed(1);
  
  if (!structure.tiers || structure.tiers.length === 0) {
    return `Flat rate: ${baseRate}% of revenue`;
  }
  
  const sortedTiers = [...structure.tiers].sort((a, b) => a.threshold - b.threshold);
  
  const tiersDisplay = [];
  
  // Add base rate from $0 fees to first tier
  const firstThreshold = sortedTiers[0].threshold;
  tiersDisplay.push(`First $${firstThreshold.toLocaleString()} in revenue: ${baseRate}%`);
  
  // Add each tier
  for (let i = 0; i < sortedTiers.length; i++) {
    const currentTier = sortedTiers[i];
    const nextTier = sortedTiers[i + 1];
    const rate = (currentTier.rate * 100).toFixed(1);
    
    if (nextTier) {
      tiersDisplay.push(`Next $${(nextTier.threshold - currentTier.threshold).toLocaleString()} in fees ($${currentTier.threshold.toLocaleString()} to $${nextTier.threshold.toLocaleString()}): ${rate}%`);
    } else {
      tiersDisplay.push(`After $${currentTier.threshold.toLocaleString()} in fees: ${rate}%`);
    }
  }
  
  if (structure.cap) {
    tiersDisplay.push(`Maximum commission rate: ${(structure.cap * 100).toFixed(1)}%`);
  }
  
  return (
    <Box>
      <Typography variant="subtitle2" gutterBottom>
        Commission Structure (based on fees generated):
      </Typography>
      {tiersDisplay.map((tier, index) => (
        <Typography key={index} variant="body2" sx={{ mb: 0.5 }}>
          {tier}
        </Typography>
      ))}
    </Box>
  );
};

  const getCommissionSummary = (employee) => {
    const structure = employee.commission_structure;
    const baseRate = (structure.base_rate * 100).toFixed(1);
    const cap = structure.cap ? (structure.cap * 100).toFixed(1) + '% cap' : 'No cap';
    
    if (!structure.tiers || structure.tiers.length === 0) {
      return `${baseRate}% (flat rate)`;
    }
    
    const sortedTiers = [...structure.tiers].sort((a, b) => a.threshold - b.threshold);
    const firstThreshold = sortedTiers[0].threshold;
    const lastThreshold = sortedTiers[sortedTiers.length - 1].threshold;
    const lastRate = (sortedTiers[sortedTiers.length - 1].rate * 100).toFixed(1);
    
    return `${structure.tiers.length + 1} tier(s)`;
  };

  const getCurrentCommissionInfo = (employee) => {
    if (!employee.cumulative_fees) {
      return {
        currentRate: (employee.commission_structure.base_rate * 100).toFixed(1),
        nextTier: employee.commission_structure.tiers?.[0] || null,
        progress: 0
      };
    }
    
    const structure = employee.commission_structure;
    const cumulative = employee.cumulative_fees;
    
    // Find current rate
    let currentRate = structure.base_rate;
    let nextTier = null;
    
    const sortedTiers = [...(structure.tiers || [])].sort((a, b) => a.threshold - b.threshold);
    
    for (let i = 0; i < sortedTiers.length; i++) {
      const tier = sortedTiers[i];
      
      if (cumulative >= tier.threshold) {
        currentRate = tier.rate;
        nextTier = sortedTiers[i + 1] || null;
      } else {
        nextTier = tier;
        break;
      }
    }
    
    // Calculate progress to next tier
    let progress = 0;
    if (nextTier && currentRate !== nextTier.rate) {
      const prevThreshold = sortedTiers.find(t => t.threshold < nextTier.threshold)?.threshold || 0;
      progress = ((cumulative - prevThreshold) / (nextTier.threshold - prevThreshold)) * 100;
      progress = Math.min(Math.max(progress, 0), 100);
    }
    
    return {
      currentRate: (currentRate * 100).toFixed(1),
      nextTier,
      progress: Math.round(progress)
    };
  };

  if (loading) {
    return (
      <Container>
        <Typography>Loading employees...</Typography>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg">
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4">Recruiters</Typography>
        <Button
          variant="contained"
          startIcon={<PersonAddIcon />}
          component={Link}
          to="/employees/new"
        >
          Add New Recruiter
        </Button>
      </Box>

      {error && (
        <Alert severity="error" sx={{ mb: 3 }}>
          {error}
        </Alert>
      )}

      {employees.length === 0 ? (
        <Paper sx={{ p: 4, textAlign: 'center' }}>
          <Typography variant="h6" gutterBottom>No Recruiters Found</Typography>
          <Typography color="textSecondary" paragraph>
            Get started by adding your first recruiter.
          </Typography>
          <Button
            variant="contained"
            startIcon={<PersonAddIcon />}
            component={Link}
            to="/employees/new"
          >
            Add First Recruiter
          </Button>
        </Paper>
      ) : (
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Name</TableCell>
                <TableCell>Email</TableCell>
                <TableCell>Current Rate</TableCell>
                <TableCell>Commission Structure</TableCell>
                <TableCell>Created</TableCell>
                <TableCell align="right">Actions</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {employees.map((employee) => {
                const commissionInfo = getCurrentCommissionInfo(employee);
                
                return (
                  <TableRow key={employee.id} hover>
                    <TableCell>
                      <Box>
                        <Typography variant="subtitle2" fontWeight="bold">
                          {employee.name}
                        </Typography>
                        <Typography variant="caption" color="textSecondary">
                          Commission YTD: ${employee.cumulative_commission?.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 }) || '0.00'}
                        </Typography>

                      </Box>
                    </TableCell>
                    
                    <TableCell>{employee.email}</TableCell>
                    
                    <TableCell>
                      <Box>
                        <Chip
                          label={`${commissionInfo.currentRate}%`}
                          size="small"
                          color="secondary"
                          sx={{ mb: 0.5 }}
                        />
                      </Box>
                    </TableCell>
                    
                    <TableCell>
                      <Box sx={{ maxWidth: 300 }}>
                        <Tooltip title={formatCommissionStructure(employee)} arrow placement="top-start">
                          <Box sx={{ display: 'flex', alignItems: 'center', cursor: 'help' }}>
                            <InfoIcon fontSize="small" sx={{ mr: 0.5, color: 'text.secondary' }} />
                            <Typography variant="body2" noWrap>
                              {getCommissionSummary(employee)}
                            </Typography>
                          </Box>
                        </Tooltip>
                        
                        {employee.commission_structure.tiers && employee.commission_structure.tiers.length > 0 && (
                          <Box sx={{ mt: 1 }}>
                            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                              {/* Base rate chip */}
                              <Chip
                                label={`$0  $${employee.commission_structure.tiers[0].threshold.toLocaleString()}: ${(employee.commission_structure.base_rate * 100).toFixed(1)}%`}
                                size="small"
                                variant="contained"
                                color="primary"
                              />
                              
                              {/* Tier chips */}
                              {employee.commission_structure.tiers
                                .sort((a, b) => a.threshold - b.threshold)
                                .map((tier, index, arr) => {
                                  const nextTier = arr[index + 1];
                                  const label = nextTier 
                                    ? `$${tier.threshold.toLocaleString()}  $${nextTier.threshold.toLocaleString()}: ${(tier.rate * 100).toFixed(1)}%`
                                    : `$${tier.threshold.toLocaleString()}+: ${(tier.rate * 100).toFixed(1)}%`;
                                  
                                  return (
                                    <Chip
                                      key={index}
                                      label={label}
                                      size="small"
                                      variant="contained"
                                      color="primary"
                                    />
                                  );
                                })}
                              
                            </Box>
                          </Box>
                        )}
                      </Box>
                    </TableCell>
                    
                    <TableCell>
                      {new Date(employee.created_at).toLocaleDateString()}
                    </TableCell>
                    
                    <TableCell align="right">
                      <IconButton
                        color="primary"
                        onClick={() => navigate(`/employees/edit/${employee.id}`)}
                        size="small"
                        title="Edit"
                      >
                        <EditIcon />
                      </IconButton>
                      <IconButton
                        color="error"
                        onClick={() => handleDeleteClick(employee.id, employee.name)}
                        size="small"
                        title="Delete"
                      >
                        <DeleteIcon />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
      )}

      <Dialog
        open={deleteDialog.open}
        onClose={handleDeleteCancel}
      >
        <DialogTitle>Delete Recruiter</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Are you sure you want to delete {deleteDialog.employeeName}? 
            THIS ACTION CANNOT BE UNDONE AND WILL DELETE ALL ASSOCIATED PLACEMENTS.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleDeleteCancel}>Cancel</Button>
          <Button onClick={handleDeleteConfirm} color="error" autoFocus>
            Delete
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default EmployeeList;