import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { 
  Grid, Card, CardContent, Typography, Box, Table, TableBody, 
  TableCell, TableContainer, TableHead, TableRow, Paper,
  Chip,
  Button,
  Alert,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Snackbar
} from '@mui/material';
import PersonIcon from '@mui/icons-material/Person';
import RestartAltIcon from '@mui/icons-material/RestartAlt';
import WarningIcon from '@mui/icons-material/Warning';

const Dashboard = () => {
  const [summary, setSummary] = useState(null);
  const [recentPlacements, setRecentPlacements] = useState([]);
  const [resetDialogOpen, setResetDialogOpen] = useState(false);
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState('');
  const [snackbarSeverity, setSnackbarSeverity] = useState('success');
  const [isResetting, setIsResetting] = useState(false);
  const [employees, setEmployees] = useState([]);

  useEffect(() => {
    fetchDashboardData();
    fetchEmployees();
  }, []);

  const fetchDashboardData = async () => {
    try {
      const response = await axios.get('/api/dashboard/summary');
      setSummary(response.data);
      setRecentPlacements(response.data.recent_placements || []);
    } catch (error) {
      console.error('Error fetching dashboard data:', error);
    }
  };

  const fetchEmployees = async () => {
    try {
      const response = await axios.get('/api/employees');
      setEmployees(response.data);
    } catch (error) {
      console.error('Error fetching employees:', error);
    }
  };


  const handleResetYTD = async () => {
    setIsResetting(true);
    try {
      const response = await axios.post('/api/employees/reset-ytd');
      
      setSnackbarMessage(response.data.message);
      setSnackbarSeverity('success');
      setSnackbarOpen(true);
      
      // Close dialog
      setResetDialogOpen(false);
      
      // Refresh dashboard data
      fetchDashboardData();
      fetchEmployees();
    } catch (error) {
      console.error('Error resetting YTD totals:', error);
      setSnackbarMessage(error.response?.data?.error || 'Failed to reset YTD totals');
      setSnackbarSeverity('error');
      setSnackbarOpen(true);
    } finally {
      setIsResetting(false);
    }
  };

  const openResetDialog = () => {
    setResetDialogOpen(true);
  };

  const closeResetDialog = () => {
    setResetDialogOpen(false);
  };


  if (!summary) return <Typography>Loading...</Typography>;

  return (
    <Box>
      <Typography variant="h4" gutterBottom sx={{ mb: 4, fontWeight: 'bold' }}>
        Dashboard
      </Typography>


    <Grid container spacing={3} sx={{justifyContent: 'flex-start'}}>







        {/* Recent Placements Table */}
        <Grid item xs={12} md={12} sx={{alignItems: 'flex-start'}}>
          <Card>
            <CardContent>
              <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                <Typography variant="h5" fontWeight={"bold"}>Recent Placements</Typography>
                <Button 
                  variant="outlined" 
                  size="small" 
                  component={Link} 
                  to="/placements"
                >
                  View All
                </Button>
              </Box>
              
              {/* ... (keep your existing recent placements table) ... */}
                            {recentPlacements.length === 0 ? (
                <Typography color="textSecondary" align="center" sx={{ py: 4 }}>
                  No placements recorded yet
                </Typography>
              ) : (
                <TableContainer component={Paper} variant="outlined">
                  <Table size="small">
                    <TableHead>
                      <TableRow>
                        <TableCell><strong>Recruiter</strong></TableCell>
                        <TableCell><strong>Candidate</strong></TableCell>
                        <TableCell><strong>Bank</strong></TableCell>
                        <TableCell><strong>Salary</strong></TableCell>
                        <TableCell><strong>Fee</strong></TableCell>
                        <TableCell><strong>Total</strong></TableCell>
                        <TableCell><strong>Commission</strong></TableCell>
                        <TableCell><strong>Date</strong></TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {recentPlacements.map((placement) => (
                        <TableRow key={placement.id} hover>
                          <TableCell>
                            <Chip
                              label={placement.employee_name || 'Unknown'}
                              size="small"
                              color="primary"
                              variant="outlined"
                            />
                          </TableCell>
                          <TableCell>
                            <Typography variant="body2">{placement.candidate_name}</Typography>
                          </TableCell>
                          <TableCell>
                            <Typography variant="body2">{placement.bank_name}</Typography>
                          </TableCell>

                          <TableCell>
                            <Typography variant="body2">
                              ${placement.starting_salary.toLocaleString()}
                            </Typography>
                          </TableCell>
                          <TableCell>
                            <Box>
                              <Typography variant="body2">
                                ({(placement.fee_percentage * 100).toFixed(1)}%)
                              </Typography>
                            </Box>
                          </TableCell>
                          <TableCell>
                              <Typography variant="body2">
                                ${(placement.starting_salary * placement.fee_percentage).toLocaleString(undefined, { minimumFractionDigits: 0, maximumFractionDigits: 0 })}
                              </Typography>
                          </TableCell>
                          <TableCell>
                            <Box>
                              <Typography variant="body2" fontWeight={"bold"} color="primary">
                                ${placement.commission_amount?.toLocaleString(undefined, { minimumFractionDigits: 0, maximumFractionDigits: 0 }) || '0'}
                              </Typography>
                            </Box>
                          </TableCell>
                          <TableCell>
                            <Typography variant="body2">
                              {new Date(placement.placement_date).toLocaleDateString()}
                            </Typography>
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </TableContainer>
              )}
            </CardContent>
          </Card>
        </Grid>


        <Grid item xs={12} md={8}>
          <Card elevation={3}>
            <CardContent>
              <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
                <Typography variant="h5" fontWeight="bold" sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                   Recruiters, {(employees).length}
                </Typography>
                <Button 
                  variant="contained" 
                  size="small" 
                  component={Link} 
                  to="/employees"
                  startIcon={<PersonIcon />}
                >
                  Manage
                </Button>
              </Box>
              
              {employees.length === 0 ? (
                <Typography color="textSecondary" align="center" sx={{ py: 4 }}>
                  No employees added yet
                </Typography>
              ) : (
                <TableContainer component={Paper} variant="outlined">
                  <Table size="small">
                    <TableHead>
                      <TableRow>
                        <TableCell><strong>Name</strong></TableCell>
                        <TableCell><strong>Email</strong></TableCell>
                        <TableCell><strong>YTD Commission</strong></TableCell>
                        <TableCell><strong>YTD Revenue</strong></TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {employees.slice(0, 8).map((employee) => (
                        <TableRow key={employee.id} hover>
                          <TableCell>
                            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                              <Typography variant="body2" fontWeight="medium">
                                {employee.name}
                              </Typography>
                            </Box>
                          </TableCell>
                          <TableCell>
                            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1}}>
                              <Typography variant="body2" fontWeight={"medium"}>
                                {employee.email}
                              </Typography>
                            </Box>
                          </TableCell>
                          <TableCell>
                            <Typography variant="body2" color="primary" fontWeight="medium">
                              ${employee.cumulative_commission?.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 }) || '0.00'}
                            </Typography>
                          </TableCell>
                          <TableCell>
                            <Typography variant="body2">
                              ${employee.cumulative_fees?.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 }) || '0.00'}
                            </Typography>
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </TableContainer>
              )}
              
              {/* Show "View All" if there are more employees */}
              {employees.length > 8 && (
                <Box sx={{ mt: 2, textAlign: 'center' }}>
                  <Button 
                    variant="outlined" 
                    size="small" 
                    component={Link} 
                    to="/employees"
                    fullWidth
                  >
                    View All {employees.length} Recruiters
                  </Button>
                </Box>
              )}
        </CardContent>
              </Card>
          </Grid>


        {/* Quick Actions & Stats */}
        <Grid item xs={12} md={4}>
          <Card sx={{ mb: 3,}}>
            <CardContent>
              <Typography variant="h6" gutterBottom>Quick Actions</Typography>
              <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, }}>
                <Button 
                  variant="contained" 
                  component={Link} 
                  to="/employees/new"
                  
                >
                  Add New Recruiter
                </Button>
                <Button 
                  variant="contained" 
                  component={Link} 
                  to="/placements/new"
                  
                  //color="success"
                >
                  Record New Placement
                </Button>
                
                {/* New Year Reset Button */}
                <Button 
                  variant="contained" 
                  color="warning"
                  
                  onClick={openResetDialog}
                  sx={{ 
                    //mt: 2,
                    background: 'linear-gradient(45deg, #d30000ff 30%, #cb0000ff 90%)',
                    color: 'white',
                    fontWeight: 'bold'
                  }}
                >
                  Start New Year
                </Button>
              </Box>
            </CardContent>
          </Card>
          

        </Grid>

      </Grid>
      {/* Reset YTD Confirmation Dialog */}
      <Dialog
        open={resetDialogOpen}
        onClose={closeResetDialog}
        aria-labelledby="reset-dialog-title"
      >
        <DialogTitle id="reset-dialog-title" sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
          
          Start New Year - Reset YTD Totals
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <Alert severity="warning" sx={{ mb: 2 }}>
              <strong>Warning:</strong> This action cannot be undone!
            </Alert>
            
            This will reset all employees' Year-to-Date (YTD) totals to zero:
            <Box component="ul" sx={{ pl: 2, mt: 1 }}>
              <li><Typography variant="body2">YTD Revenue  $0.00</Typography></li>
              <li><Typography variant="body2">YTD Commission  $0.00</Typography></li>
            </Box>
            
            <Typography variant="body2" sx={{ mt: 2 }}>
              This should only be done at the beginning of a new year.
              All placements will remain, but employee tier calculations will reset.
            </Typography>
            
            <Typography variant="body2" fontWeight="bold" color="error" sx={{ mt: 2 }}>
              Are you sure you want to proceed?
            </Typography>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={closeResetDialog} disabled={isResetting}>
            Cancel
          </Button>
          <Button 
            onClick={handleResetYTD} 
            color="error" 
            variant="contained"
            disabled={isResetting}
            startIcon={isResetting ? <RestartAltIcon /> : <WarningIcon />}
          >
            {isResetting ? 'Resetting...' : 'Yes, Reset All YTD Totals'}
          </Button>
        </DialogActions>
      </Dialog>
      
      {/* Snackbar for notifications */}
      <Snackbar
        open={snackbarOpen}
        autoHideDuration={5000}
        onClose={() => setSnackbarOpen(false)}
      >
        <Alert 
          onClose={() => setSnackbarOpen(false)} 
          severity={snackbarSeverity}
          sx={{ width: '100%' }}
        >
          {snackbarMessage}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default Dashboard;